
import { Editor, EditorContent } from '@tiptap/vue-2'
import StarterKit from '@tiptap/starter-kit'
import Vue from "vue";

export default Vue.extend({
  name: "RichEditor-View",
  components: {
    EditorContent,
  },
  data() {
    return {
      editor: undefined as unknown as Editor,
      value: {
        type: String,
        default: ''
      },
      textLevels: [
          'Text',
          'part',
          'section',
          'subsection',
          'subsubsection',
          'paragraph',
          'subparagraph'
      ],
      level: 'Text'
    }
  },

  mounted() {
    this.editor = new Editor({
      content: '<p>I’m running Tiptap with Vue.js. 🎉</p>',
      extensions: [
        StarterKit,
      ],
      onUpdate: () => {
        // HTML
        console.log('u');

        // JSON
        // this.$emit('input', this.editor.getJSON())
      }
    })
  },

  watch: {
    value(value) {
      // HTML
      const isSame = this.editor.getHTML() === value

      // JSON
      // const isSame = JSON.stringify(this.editor.getJSON()) === JSON.stringify(value)

      if (isSame) {
        return
      }

      this.editor.commands.setContent(value, false)
    },
    currentHeadingLevel(lv) {
      console.log(lv)
      this.$data.level = 'Text';
    }
  },

  computed: {
    bannerAccepted(): boolean {
      return window.localStorage.getItem('texThesorEditorAccepted') !== 'true'
    },
    currentHeadingLevel() {
      if( this.$data.editor) {
        return this.$data.editor.getAttributes('heading')
      }
      return ''
    }
  },

  methods: {
    acceptBanner(dismiss: ()=>void) {
      dismiss()
      window.localStorage.setItem('texThesorEditorAccepted', 'true');
    },
    handleLevelChange() {
      console.log(this.level)
      switch (this.level) {
        case 'Text':
          this.editor.chain().focus().setParagraph().run()
          break;
        case 'part':
          this.editor.chain().focus().toggleHeading({ level: 1 }).run();
          break;
        case 'section':
          this.editor.chain().focus().toggleHeading({ level: 2 }).run();
          break;
        case 'subsection':
          this.editor.chain().focus().toggleHeading({ level: 3 }).run();
          break;
        case 'subsubsection':
          this.editor.chain().focus().toggleHeading({ level: 4 }).run();
          break;
        case 'paragraph':
          this.editor.chain().focus().toggleHeading({ level: 5 }).run();
          break;
        case 'subparagraph':
          this.editor.chain().focus().toggleHeading({ level: 6 }).run();
          break;
      }
    }
  },

  beforeDestroy() {
    this.editor.destroy()
  },
})
